<template><undefined>
 
<h1>Carousel 走马灯</h1>
<p>旋转木马，一组轮播的区域。</p>
<h2 id="he-shi-shi-yong"><a class="header-anchor" href="#he-shi-shi-yong" aria-hidden="true">¶</a> 何时使用</h2>
<ul>
<li>当有一组平级的内容。</li>
<li>当内容空间不足时，可以用走马灯的形式进行收纳，进行轮播展现。</li>
<li>常用于一组图片或卡片轮播。</li>
</ul>
<h2 id="dai-ma-yan-shi"><a class="header-anchor" href="#dai-ma-yan-shi" aria-hidden="true">¶</a> 代码演示</h2>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;v-carousel v-model=\&quot;value1\&quot;&gt;\n      &lt;v-carousel-item&gt;\n          &lt;div class=\&quot;demo-carousel\&quot;&gt;1&lt;/div&gt;\n      &lt;/v-carousel-item&gt;\n      &lt;v-carousel-item&gt;\n          &lt;div class=\&quot;demo-carousel\&quot;&gt;2&lt;/div&gt;\n      &lt;/v-carousel-item&gt;\n      &lt;v-carousel-item&gt;\n          &lt;div class=\&quot;demo-carousel\&quot;&gt;3&lt;/div&gt;\n      &lt;/v-carousel-item&gt;\n      &lt;v-carousel-item&gt;\n          &lt;div class=\&quot;demo-carousel\&quot;&gt;4&lt;/div&gt;\n      &lt;/v-carousel-item&gt;\n  &lt;/v-carousel&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data() {\n    return {\n      value: 0\n    }\n  },\n}\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
  <v-carousel v-model="value1">
      <v-carousel-item>
          <div class="demo-carousel">1</div>
      </v-carousel-item>
      <v-carousel-item>
          <div class="demo-carousel">2</div>
      </v-carousel-item>
      <v-carousel-item>
          <div class="demo-carousel">3</div>
      </v-carousel-item>
      <v-carousel-item>
          <div class="demo-carousel">4</div>
      </v-carousel-item>
  </v-carousel>
</template>


</div>
