<h2 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h2>
<h3 id="carousel-props"><a class="header-anchor" href="#carousel-props" aria-hidden="true">¶</a> Carousel Props</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>幻灯片的索引，从 0 开始，可以使用 v-model 双向绑定数据</td>
<td>Number</td>
<td>0</td>
</tr>
<tr>
<td>height</td>
<td>走马灯的高度，可填 auto 或具体高度数值，单位 px</td>
<td>String</td>
<td>Number</td>
</tr>
<tr>
<td>autoplay</td>
<td>是否自动切换</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>autoplaySpeed</td>
<td>自动切换的时间间隔，单位为毫秒</td>
<td>Number</td>
<td>2000</td>
</tr>
<tr>
<td>dots</td>
<td>指示器的位置，可选值为 inside （内部），outside（外部），none（不显示）</td>
<td>String</td>
<td>inside</td>
</tr>
<tr>
<td>trigger</td>
<td>指示器的触发方式，可选值为 click（点击），hover（悬停）</td>
<td>String[]</td>
<td>click</td>
</tr>
<tr>
<td>arrow</td>
<td>切换箭头的显示时机，可选值为 hover（悬停），always（一直显示），never（不显示）</td>
<td>String</td>
<td>hover</td>
</tr>
<tr>
<td>easing</td>
<td>动画效果</td>
<td>String</td>
<td>ease</td>
</tr>
<tr>
<td>vertical</td>
<td>垂直显示</td>
<td>Boolean</td>
<td>false</td>
</tr>
</tbody>
</table>
<h3 id="carousel-events"><a class="header-anchor" href="#carousel-events" aria-hidden="true">¶</a> Carousel Events</h3>
<table class="table"><thead>
<tr>
<th>事件</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>幻灯片切换时触发</td>
<td>(oldValue, value)</td>
</tr>
</tbody>
</table>
</undefined></template>
<style scoped>
.demo-carousel {
    height: 200px;
    line-height: 200px;
    text-align: center;
    color: #fff;
    font-size: 20px;
    background: #364d79;
}
</style>
<script>
export default {
    data() {
        return {
            value1: 0,
            value2: 0,
            setting: {
                autoplay: false,
                autoplaySpeed: 2000,
                dots: 'inside',
                trigger: 'click',
                arrow: 'hover'
            },
            labelCol: {
                span: 6
            },
            wrapperCol: {
                span: 14
            }
        }
    },
}
</script>


